.product {
  background-color: azure;
  padding: 15px 20px;
  border-radius: 25px;
  display: grid;
  grid-template-columns: auto 1fr auto;
}

.productInfo {
  padding-left: 25px;
}
.productPrice {
  width: 70px;
  height: 40px;
  background-color: rgb(243, 236, 203);
  border-radius: 25px;
  cursor: pointer;
}
.productPrice:active {
  font-size: 20px;
  color: green;
}
.productCheckout {
  display: grid;
  grid-template-rows: 1fr auto;
  justify-items: end;
  font-size: 15px;
}

.productDelete {
  background-color: none;
  padding: 5px 10px;
  color: var(--accent-1);
  border-color: var(--accent-2);
  font-size: 15px;
  border-radius: 15px;
  color: red;
  cursor: pointer;
}
.productDelete:active {
  font-size: 20px;
}

.productImageContainer {
  position: relative;
}

.productQuantityContainer {
  position: absolute;
  top: 8px;
  right: -15px;
  background-color: rgb(223, 245, 231);
  border-radius: 50%;
  padding: 5px;
}

.productQuantity {
  background-color: var(--primary-1);
  color: rgb(7, 71, 55);
  border-radius: 50%;
  font-size: 14px;
  width: 23px;
  height: 23px;
  display: grid;
  place-items: center;
}

.productImage {
  background-color: var(--neutral-5);
  padding: 10px;
  border-radius: 20px;
  transition: transform 180ms ease-out;
}
.product:nth-child(2n):hover .productImage {
  transform: scale(1.01) rotateZ(2deg);
}
.product:nth-child(2n + 1):hover .productImage {
  transform: scale(1.01) rotateZ(-2deg);
}

.product h3,
.product p {
  margin: 0;
}